import React, { useState } from "react"
import { Data } from "../utils/chart_data"
import { Line } from "react-chartjs-2"
import { CategoryScale } from "chart.js"
import Chart from "chart.js/auto"
Chart.register(CategoryScale)

function LineChart({color}) {
    const [chartData] = useState({
        labels: Data.map(data => data.gradeYear),
        datasets: [
          {
            label: "GPA",
            data: Data.map(data => data.grade), 
            borderColor: color,
            backgroundColor: color,
            tension: 0.4,
          }
        ]
      })
    return (
        <React.Fragment>
            <Line
                data={chartData}
                options={{
                    responsive: true,
                    maintainAspectRatio: false,
                    plugins: {
                        title: {
                            display: true,
                        },
                        legend: {
                            display: false
                        }
                    },
                    scales: {
                        y: {
                            min: 0.0,
                            max: 4.0,
                            ticks: {
                                stepSize: 0.5
                            }
                        }
                    }
                }}
            />
        </React.Fragment>
    );
}

export default LineChart;