import './index.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import React, { useState, useEffect, useRef } from 'react'

import { AcademicData, GradeData } from '../../utils/academic_data'
import LineChart from '../../components/linechart'


export const Academic = () => {
    const [isVisible, setIsVisible] = useState(true)
    return (
        <div className='app' style={{
            
        }}>
            <div className='section-cover-photo section-1 position-fixed top-0 start-0 vw-100 vh-80' style={{ 
                background: `url(${AcademicData.cover_photo}) no-repeat`,
                display: isVisible ? 'block' : 'none'
            }}/>
            <a className='back raleway-regular'>
                 Back
            </a>
            <div className='section-content d-flex flex-column w-100'>
                <div className='fake-bg position-absolute w-100 vh-100'></div>
                <h1 className='section-title montserrat-black'>
                    {AcademicData.title}
                </h1>
                <div className='section-block d-flex align-items-center justify-content-center mx-custom mt-4'>
                    <div className='large-chart py-custom'>
                        <LineChart color={AcademicData.color}/>
                    </div>
                </div>

                <div className='section-card-container d-flex align-items-start justify-content-between mx-custom mt-4'>
                    {
                        GradeData.map((grade, i) => {
                            return (
                                <SectionCard data={grade}/>
                            )
                        })
                    }
                </div>
            </div>
        </div>
    )
}

function SectionCard({data}) {
    return (
        <div className='section-card'>
            <div className='section-card-header d-flex align-items-end justify-content-around'>
                <div className='left'>
                    <h5 className='raleway-regular'>{data.grade}</h5>
                    <div className='d-flex'>
                        <h1 className='montserrat-black'>{data.gpa}</h1>
                        {/* <p className='raleway-regular'>GPA</p> */}
                    </div>  
                </div>
                <div className='right d-flex justify-content-end'>
                    <h4 className='raleway-regular'>{data.year}</h4>
                </div>
            </div>
            <div className='section-card-content d-flex flex-column'>
                <img src={data.img}/>
                <div className='download-btn w-75 raleway-regular'>
                    Download PDF
                </div>
            </div>
        </div>
    )
}