import './index.css'
import 'bootstrap/dist/css/bootstrap.min.css'

import React, { useState, useEffect, useRef } from 'react'

import LineChart from '../../components/linechart'

import gsap from 'gsap'
import { useGSAP } from '@gsap/react'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

import { HeaderData, Sections } from '../../utils/home_data'
import { useNavigate } from "react-router-dom"

gsap.registerPlugin(useGSAP);

export const Home = () => {

  const [isVisible, setIsVisible] = useState(true)
  const LearnMoreAnimation = useRef(null)
  const AchievementAnimation = useRef(null)
  const Navigate = useNavigate()

  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY;
      const triggerHeight = window.innerHeight * 2.1;

      if (scrollPosition > triggerHeight) {
        setIsVisible(false);
      } else {
        setIsVisible(true);
      }
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [])

  useEffect(() => {

    gsap.registerPlugin(ScrollTrigger)
    const tl = gsap.timeline()
    tl.to('.header-name h1 .word', 0.75, {
      y: 0,
      stagger: 0.2,
      delay: 0.2,
      ease: "power2.out"
    })

    tl.to('.header-name h5 .word', 0.5, {
      y: 0,
      stagger: 0.05,
      delay: 0.2,
      ease: "power2.out"
    }, '<')

    tl.to('.box-animate', 0.5, {
      x: 0,
      opacity: 1,
      stagger: 0.2,
      ease: "power2.out"
    }, '<')

    const infinite_tl = gsap.timeline()

    infinite_tl.fromTo('.header-grade .orange-highlight .span_wrap', 0.5, {
      yPercent: 0,
    }, {
      yPercent: -5,
      stagger: {
        each: 0.1,
        yoyo: true,
        repeat: -1,
        repeatDelay: 1
      }
    }, '+=3')    

    var sections = gsap.utils.toArray('.section')

    sections.forEach((section, i) => {
      const anim = gsap.fromTo(section, 0.2, {
        yPercent: -100,
        opacity: 0,
      }, {
        yPercent: 0,
        opacity: 1,
        delay: i * (0.03),
        ease: "power2.out"
      }, '<')

      ScrollTrigger.create({
        trigger: section,
        start: '-30% 100%',
        animation: anim,
        once: true,
      })
    });

    const tl_scroll = gsap.timeline()
      
    var intro_words = gsap.utils.toArray('.intro-paragraph .word')

    intro_words.forEach((word, i) => {
      const anim = tl_scroll.to(word, 0.2, {
        y: 0,
        opacity: 1,
        ease: "power2.out"
      }, i * 0.005)

      ScrollTrigger.create({
        trigger: '.introduction-content',
        start: '0 80%',
        animation: anim,
        once: true
      })
    });

    const tl_scroll_2 = gsap.timeline()

    var welcome_words = gsap.utils.toArray('.intro-welcome .word')

    welcome_words.forEach((word, i) => {
      const anim = tl_scroll_2.to(word, 0.2, {
        y: 0,
        opacity: 1,
        ease: "power2.out"
      }, i * 0.05)

      ScrollTrigger.create({
        trigger: '.introduction-content',
        start: '0 80%',
        animation: anim,
        once: true
      })
    });
    
    
  }, [])
  
  useEffect(() => {
      
    const tl_1 = gsap.timeline({paused: true})

    tl_1.to(`.header-grade .mask .letter`, 0.2, {
        yPercent: -100,
        stagger: 0.02,
        ease: 'power2.out'
    })

    tl_1.to(`.header-grade .mask .hidden-letter`, 0.2, {
        yPercent: -100,
        stagger: 0.02,
        ease: 'power2.out'
    }, '<')

    AchievementAnimation.current = tl_1

    const tl_2 = gsap.timeline({paused: true})

    tl_2.to(`.header-swimmer-info .mask .letter`, 0.3, {
      yPercent: -100,
      stagger: 0.03,
      ease: 'power2.out'
  })

    tl_2.to(`.header-swimmer-info .mask .hidden-letter`, 0.3, {
        yPercent: -100,
        stagger: 0.03,
        ease: 'power2.out'
    }, '<')

    LearnMoreAnimation.current = tl_2
    
  }, [])

  function onAnimEnter(animation) {
      if (animation) {
          animation.play();
      }
  }

  function onAnimLeave(animation) {
      if (animation) {
          animation.reverse();
      }
  }

  const rowSections = []

  for (let i = 0; i < Sections.length; i += 2) {
    rowSections.push(Sections.slice(i, i + 2));
  }

  return (
    <div className='app'>
      <div className='header-photo position-fixed top-0 start-0 vw-100 vh-100' style={{ display: isVisible ? 'block' : 'none' }}/>
      <div className='header-content d-flex mx-custom'>
        <div className='header-name d-flex-column text-white'>
          <h1 className='montserrat-black'>
            {
              HeaderData.name.split(' ').map((name, i) => (
                <React.Fragment>
                  <span className='mask' key={i}>
                    <span className='word' key={i}>
                      {name}
                    </span>
                  </span><br/>
                </React.Fragment>
              ))
            }
          </h1>
          <h5 className='raleway-regular'>
            {
              HeaderData.description.split('//').map((part, i) => (
                <React.Fragment>
                  {part.split(' ').map((word, j) => (
                    <span className='mask' key={j}>
                      <span className='word' key={j}>
                        {word}
                      </span>
                    </span>
                  ))}
                  {i < HeaderData.description.split('//').length - 1 && <br/>}
                </React.Fragment>
              ))
            }
          </h5>
        </div>
        <div className='header-right d-flex-column'>
          <div className='header-description-mobile py-custom box-custom box-animate'>
            <h5 className='raleway-regular'>
              {
                HeaderData.description.split('//').map((part, i) => (
                  <React.Fragment>
                    {part.split(' ').map((word, j) => (
                      <React.Fragment>
                        {word}{' '}
                      </React.Fragment>
                    ))}
                    {i < HeaderData.description.split('//').length - 1 && <br/>}
                  </React.Fragment>
                ))
              }
            </h5>
          </div>
          <div className='header-grade py-custom box-custom box-animate'>
            <h5 className='raleway-regular'>A LEVEL STUDENT WITH PREDICTED GRADES:</h5>
            <Grade/>
            <p className='raleway-regular'>2 raw marks away from highest mark in the world in AS-Level physics</p>
            <a className='raleway-regular d-inline-flex flex-wrap'
                onMouseEnter={() => onAnimEnter(AchievementAnimation.current)}
                onMouseLeave={() => onAnimLeave(AchievementAnimation.current)}
                onClick={() => Navigate('/academic')}
            >
            {
                HeaderData.achievement.split(' ').map((word, i) => (
                  <span className='d-inline-flex'>
                    {
                      word.split('').map((letter, i) => (
                        <span className='mask'>
                          <span className='letter' key={i}>
                            {letter}
                          </span>
                          <span className='hidden-letter' key={i} >
                            {letter}
                          </span>
                        </span>
                      ))
                    }
                    <span className='space-right'></span>
                  </span>
                ))
              }
            </a>
          </div>
          <div className='header-swimmer-info py-custom box-custom box-animate'>
            <h5 className='raleway-regular'>COMPETITIVE SWIMMER WHO HAS:</h5>
            <ul>
              <li className='raleway-regular'>Secured 2nd and 3rd place in the Middle East open and junior Championships held in Dubai with competitors from all over the middle east region</li>
            </ul>
            <a className='raleway-regular d-inline-flex' href='#'
                onMouseEnter={() => onAnimEnter(LearnMoreAnimation.current)}
                onMouseLeave={() => onAnimLeave(LearnMoreAnimation.current)}
            >
              {
                HeaderData.button.split(' ').map((word, i) => (
                  <React.Fragment>
                    {
                      word.split('').map((letter, i) => (
                        <span className='mask'>
                          <span className='letter' key={i}>
                            {letter}
                          </span>
                          <span className='hidden-letter' key={i} >
                            {letter}
                          </span>
                        </span>
                      ))
                    }
                    <span className='space-right'></span>
                  </React.Fragment>
                ))
              }
            </a>
          </div>
          <div className='header-grade-chart py-custom box-custom box-animate'>
            <h5 className='raleway-regular'>GPA CHART</h5>
              <div className='small-chart'>
                <LineChart color={"#E46E00"}/>
              </div>
          </div>
        </div>
      </div>
      <div className='main-wrapper'>
        <div className='introduction-content py-custom box-custom mx-custom'>
          <p className='raleway-regular'>
            <span className='intro-welcome raleway-regular'>
              {
                HeaderData.welcome.split(' ').map((word, i) => {
                  return(
                    <span className='mask'>
                      <span className='word'>
                        {word}
                      </span>
                    </span>
                  )
                })
              }
            </span><br/><br/>
            <span className='intro-paragraph align-items-center' style={{verticalAlign: 'middle'}}>
              {
                HeaderData.intro.split('{GRADE}').map((phrase, i) => {
                  return(
                    <React.Fragment>
                      {
                        phrase.split('//').map((breakline, j) => {
                          return(
                            <span>
                              {
                                breakline.split(' ').map((word, k) => {
                                  return(
                                    <span className='mask'>
                                      <span className='word'>
                                        {word === 'Vice' || word === 'President' ? <b>{word}</b> : word}
                                      </span>
                                    </span>
                                  )
                                })
                              }
                              {i !== 0 && <br/>}
                            </span>
                          )
                        })
                      }
                      {i ===  0 && <span className='mask no-overflow'><span className='word'><Grade size='small'/></span></span>}
                    </React.Fragment>
                  )
                })
              }
            </span>
          </p>
        </div>
        <div className='section-selection py-custom box-custom mx-custom'>
          <h5 className='raleway-regular'>Please select a section to explore</h5>
          <div className='sections-container'>
            {
              Sections.map((section, i) => {
                if (i % 3 === 0) {
                  return (
                    <div className="row">
                      {Sections.slice(i, i + 3).map((subSection, j) => (
                        <Section data={subSection} key={j}/>
                      ))}
                    </div>
                  )
                }
                return null
              })
            }
          </div>
        </div>
      </div>
    </div>
  );
}

function Section({data}) {
  const Navigate = useNavigate()
  
  return(
    <div className='section-mask col m-custom px-0' onClick={() => Navigate(data.link)}>
      <div className='section d-flex align-items-end'
          style={{
              background: `url(${data.img}) no-repeat center center`
            }}>
        <div className='section-text-wrapper d-flex align-items-center'>
          <h1 className='raleway-regular text-white'>{data.title}</h1>
        </div>
      </div>
    </div>
  );
}

function Grade({size}) {

  const grades = Array(3).fill(null)


  return(
    <span className='highlight d-inline-flex orange-highlight montserrat-black' style={{verticalAlign: 'middle'}}>
      {
        grades.map((_, i) => (
          <span className={`span_wrap ${size}`} key={i}>
            <span>A</span>
            <sup className={`sup_wrap ${size}`}><span>*</span></sup>
          </span>
        ))
      }
    </span> 
  );
}